                                    </div>
                                </div>
                                
                                <!-- Group By Section (only for Summary) -->
                                <div class="row" id="divSelectedColumnsDataGroupBy" v-if="selectedTableHeader === 'tblInvoiceSummary'" style="display: block;">
                                    <div class="heading-border mt-3">
                                        <h5 class="h5"><span>{{ $t('GroupBy') }}</span></h5>
                                    </div>
                                    <div class="py-2 d-inline-block">
                                <span class=""></span> &nbsp;
                            </div>
                                    <div class="listing mt-3">
                                        <div class="table-responsive  paddingcontrol table-fix-header" style="max-height:250px; min-height:250px;">
                                            <table class="table table-bordered">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th scope="col" style="width:35%; cursor:auto;">{{ $t('fieldName') }}</th>
                                                        <th scope="col" style="width:33%; cursor:auto;">{{ $t('DisplayName') }}</th>
                                                        <th scope="col" style="width:32%; cursor:auto;" class="thchangeheader text-center">{{ $t('Action') }}</th>
                                                    </tr>
                                                </thead>
                                                <tbody id="visiblecolumnGroupBy">
                                                    <tr
                                                        v-for="(item, index) in selectedGroupByColumns"
                                                        :key="'groupby_' + index"
                                                    >
                                                        <td style="width:35%;">
                                                            <div class="form-check form-check-inline">
                                                                <div class="custom-control custom-checkbox custom-control-inline">
                                                                    <input
                                                                        type="checkbox"
                                                                        class="chkvisibleitemsgroup custom-control-input"
                                                                        :id="'Checkbox_groupby_' + index"
                                                                        :value="item.templateId"
                                                                        :data-resource-id="item.displayName"
                                                                        :data-columnname="item.columnName"
                                                                        :data-datatype="item.dataType"
                                                                        :data-tablename="item.tableName"
                                                                        :data-displayname="item.displayName"
                                                                        :data-from="item.fieldFrom"
                                                                        v-model="item.selected"
                                                                    />
                                                                    <label
                                                                        class="custom-control-label"
                                                                        :for="'Checkbox_groupby_' + index"
                                                                        style="margin-left: 5px;">
                                                                        {{ item.columnName }}
                                                                    </label>
                                                                </div>
                                                            </div>
                                                        </td>
                                                        <td style="width:35%;">
                                                            <input
                                                                type="text"
