                                                                v-model="item.displayName"
                                                                maxlength="100"
                                                            />
                                                        </td>
                                                        
                                                        <td style="width:5%;" class="single-action text-center">
                                                            <a 
                                                                class="aGroupColumnRemove" 
                                                                href="javascript:;" 
                                                                :title="$t('Delete')"
                                                                @click="removeGroupByColumn(index)"
