                            </div>
                        </div>
                        
                        <div class="col-lg-12 px-0 text-right mt-2 float-left">
                            <a href="javascript:;" class="btn btn-success mr-2" id="ancSaveHeaderTemplateData" @click="save" :title="$t('Save')">
                                <i class="fa fa-save pr-2"></i>{{ $t('Save') }}
                            </a>
                            <a href="javascript:;" class="btn btn-danger" id="ancCancelHeaderTemplateData" @click="close" :title="$t('Cancel')">
                                <i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}
