</template>

<script>
import DataService from '../../../services/DataService';
import  { locateBillingService } from '../../../services/locateBillingService'

export default {
    name: "TemplateFieldMapping",
    props: {
        setupId: {
            type: [Number, String],
            required: true
        },
        selectedTableHeader: {
            type: String,
            required: true
        },
        existingFields: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            isLoading: false,
            availableColumnsInSetup: [],
            availableColumnsInTalygen: [],
            selectedColumns: [],
            selectedGroupByColumns: [],
            searchSetup: '',
            searchTalygen: '',
            noOfColumn: 50, // Default, should come from props or API
            dateFormatOptions: [
                { Text: "YYYY/MM/DD", Value: "yyyy/MM/dd" },
                { Text: "MM/DD/YYYY", Value: "MM/dd/yyyy" },
                { Text: "DD/MM/YYYY", Value: "dd/MM/yyyy" },
                { Text: "12 Hours", Value: "hh:mm tt" },
                { Text: "24 Hours", Value: "HH:mm" }
            ]
        }
    },
    computed: {
        filteredAvailableColumnsInSetup() {
            var vm = this;
            // Return original array - visibility is handled by visible property on original objects
            return vm.availableColumnsInSetup;
        },
        filteredAvailableColumnsInTalygen() {
            var vm = this;
            // Return original array - visibility is handled by visible property on original objects
            return vm.availableColumnsInTalygen;
        }
    },
    watch: {
        searchSetup(newVal) {
            this.updateSetupVisibility();
        },
        searchTalygen(newVal) {
            this.updateTalygenVisibility();
        },
        availableColumnsInSetup: {
            handler() {
                this.updateSetupVisibility();
            },
            deep: true,
            immediate: true
        },
        availableColumnsInTalygen: {
            handler() {
                this.updateTalygenVisibility();
            },
            deep: true,
            immediate: true
        }
    },
    created() {
        var vm = this;
        vm.loadFieldMappingData();
        vm.loadExistingFields();
    },
    methods: {
        updateSetupVisibility() {
            var vm = this;
            if (!vm.searchSetup || vm.searchSetup.trim() === '') {
                vm.availableColumnsInSetup.forEach(item => {
                    vm.$set(item, 'visible', true);
                });
            } else {
                var searchText = vm.searchSetup.trim().toUpperCase();
                vm.availableColumnsInSetup.forEach(item => {
                    var isVisible = item.displayName && item.displayName.toUpperCase().indexOf(searchText) >= 0;
                    vm.$set(item, 'visible', isVisible);
                });
            }
        },
        updateTalygenVisibility() {
            var vm = this;
            if (!vm.searchTalygen || vm.searchTalygen.trim() === '') {
                vm.availableColumnsInTalygen.forEach(group => {
                    if (group.availableColumnsInTalygenGroup) {
                        group.availableColumnsInTalygenGroup.forEach(item => {
                            vm.$set(item, 'visible', true);
                        });
                    }
                });
            } else {
                var searchText = vm.searchTalygen.trim().toUpperCase();
                vm.availableColumnsInTalygen.forEach(group => {
                    if (group.availableColumnsInTalygenGroup) {
                        group.availableColumnsInTalygenGroup.forEach(item => {
                            var isVisible = item.displayName && item.displayName.toUpperCase().indexOf(searchText) >= 0;
                            vm.$set(item, 'visible', isVisible);
                        });
                    }
                });
            }
        },
        getModalTitle() {
            var vm = this;
            var title = vm.$t('Manage') + ' ';
            switch(vm.selectedTableHeader) {
                case 'tblInvoiceSummary':
                    title += vm.$t('Summary');
                    break;
                case 'tblDetailInvoiceFormat':
                    title += vm.$t('DetailedInvoiceFormat');
                    break;
                case 'tblDetailedInvoiceFormat':
                    title += vm.$t('DetailsInvoiceFormat');
                    break;
                case 'tblInvoiceEditMode':
                    title += vm.$t('InvoiceEditMode');
                    break;
                default:
                    title += vm.$t('Template');
            }
            return title;
        },
        getFormatLabel() {
            var vm = this;
            if (vm.selectedTableHeader === 'tblInvoiceSummary') {
                return vm.$t('Action');
            }
            return vm.$t('Format');
        },
        loadFieldMappingData() {
            var vm = this;
            vm.isLoading = true;
            var url = `setupId=${vm.setupId}`;
            debugger
            // TODO: Replace with actual API call
            locateBillingService.GetTemplateFieldMapping(url).then((response) => {
                vm.isLoading = false;
                if (response.data.result) {
                    vm.availableColumnsInSetup = response.data.result.availableColumnsInSetup || [];
                    vm.availableColumnsInTalygen = response.data.result.availableColumnsInTalygen || [];
                    vm.noOfColumn = response.data.result.noOfColumn || 50;
                    vm.availableColumnsInSetup.forEach(item => {
                        vm.$set(item, 'selected', false);
                    });
                    vm.availableColumnsInTalygen.forEach(group => {
                        vm.$set(group, 'selected', false);
                        if (group.availableColumnsInTalygenGroup) {
                            group.availableColumnsInTalygenGroup.forEach(item => {
                                vm.$set(item, 'selected', false);
                            });
                        }
                    });
                }
            }).catch((error) => {
                vm.isLoading = false;
                console.error('Error loading field mapping data:', error);
            });
        },
        loadExistingFields() {
            var vm = this;
            if (vm.existingFields) {
                if (vm.selectedTableHeader === 'tblInvoiceSummary') {
                    vm.selectedColumns = (vm.existingFields.summary || []).map(item => ({
                        ...item,
                        selected: false
                    }));
                    vm.selectedGroupByColumns = (vm.existingFields.groupBy || []).map(item => ({
                        ...item,
                        selected: false
                    }));
                } else {
                    vm.selectedColumns = vm.existingFields.map(item => ({
                        ...item,
                        selected: false
                    }));
                }
            }
        },
        searchAvailableColumnsInSetup() {
            // Search is handled by watcher on searchSetup
            this.updateSetupVisibility();
        },
        clearSearchSetup() {
            this.searchSetup = '';
            this.updateSetupVisibility();
        },
        searchAvailableColumnsInTalygen() {
            // Search is handled by watcher on searchTalygen
            this.updateTalygenVisibility();
        },
        clearSearchTalygen() {
            this.searchTalygen = '';
            this.updateTalygenVisibility();
        },
        toggleGroupSelection(group) {
            var vm = this;
            if (group.availableColumnsInTalygenGroup) {
                group.availableColumnsInTalygenGroup.forEach(item => {
                    item.selected = group.selected;
                });
            }
        },
        updateGroupSelection(group) {
            var vm = this;
            var allSelected = group.availableColumnsInTalygenGroup.every(item => item.selected);
            group.selected = allSelected;
        },
        moveToSelected() {
            var vm = this;
            var selectedItems = [];
            // Get selected items from Setup
            vm.availableColumnsInSetup.forEach(item => {
                if (item.selected && item.visible !== false) {
                    // Check if already exists in selected columns
                    var exists = vm.selectedColumns.some(sel =>
                        sel.columnName === item.columnName &&
                        sel.tableName === item.tableName &&
                        sel.fieldFrom === 'setup'
                    );
                    
                    if (!exists) {
                        selectedItems.push({
                            templateId: item.templateId,
                            columnName: item.columnName,
                            dataType: item.dataType,
                            tableName: item.tableName,
                            displayName: item.displayName,
                            fieldFrom: 'setup',
                            formatType: '',
                            selected: false
                        });
                    }
                    item.selected = false;
                }
            });
            
            // Get selected items from Talygen
            vm.availableColumnsInTalygen.forEach(group => {
                if (group.availableColumnsInTalygenGroup) {
                    group.availableColumnsInTalygenGroup.forEach(item => {
                        if (item.selected && item.visible !== false) {
                            // Check if already exists
                            var exists = vm.selectedColumns.some(sel =>
                                sel.columnName === item.columnName &&
                                sel.tableName === item.tableName &&
                                sel.fieldFrom === 'talygen'
                            );

                            if (!exists || (item.dataType === 'Date Time' || item.dataType === 'Time')) {
                                selectedItems.push({
                                    templateId: item.templateId,
                                    columnName: item.columnName,
                                    dataType: item.dataType,
                                    tableName: item.tableName,
                                    displayName: item.displayName,
                                    fieldFrom: 'talygen',
                                    formatType: '',
                                    selected: false
                                });
                            }
                            item.selected = false;
                        }
                    });
                    group.selected = false;
                }
            });
            
            // Check column limit
            if (vm.selectedColumns.length + selectedItems.length > vm.noOfColumn) {
                // Show alert: More than allowed columns
                alert(vm.$t('TemplateAlertMessage') ? vm.$t('TemplateAlertMessage').replace('{0}', vm.noOfColumn) : 'Maximum ' + vm.noOfColumn + ' columns allowed');
                return;
            }
            
            if (selectedItems.length > 0) {
                vm.selectedColumns = vm.selectedColumns.concat(selectedItems);
            }
        },
        removeFromSelected() {
            var vm = this;
            var itemsToRemove = vm.selectedColumns.filter(item => item.selected);
            
            if (itemsToRemove.length === 0) {
                return;
            }
            
            itemsToRemove.forEach(item => {
                // Remove from selected columns
                var index = vm.selectedColumns.findIndex(sel =>
                    sel.templateId === item.templateId &&
                    sel.fieldFrom === item.fieldFrom &&
                    sel.columnName === item.columnName &&
                    sel.tableName === item.tableName
                );
                if (index >= 0) {
                    vm.selectedColumns.splice(index, 1);
                }

                // Also remove from GroupBy if exists
                if (vm.selectedTableHeader === 'tblInvoiceSummary') {
                    var groupByIndex = vm.selectedGroupByColumns.findIndex(sel =>
                        sel.templateId === item.templateId &&
                        sel.fieldFrom === item.fieldFrom &&
                        sel.columnName === item.columnName &&
                        sel.tableName === item.tableName
                    );
                    if (groupByIndex >= 0) {
                        vm.selectedGroupByColumns.splice(groupByIndex, 1);
                    }
                }
            });
        },
        removeColumn(index) {
            var vm = this;
            if (index >= 0 && index < vm.selectedColumns.length) {
                var item = vm.selectedColumns[index];
                vm.selectedColumns.splice(index, 1);
                
                // Also remove from GroupBy if exists
                if (vm.selectedTableHeader === 'tblInvoiceSummary') {
                    var groupByIndex = vm.selectedGroupByColumns.findIndex(sel =>
                        sel.templateId === item.templateId &&
                        sel.fieldFrom === item.fieldFrom &&
                        sel.columnName === item.columnName &&
                        sel.tableName === item.tableName
                    );
                    if (groupByIndex >= 0) {
                        vm.selectedGroupByColumns.splice(groupByIndex, 1);
                    }
                }
            }
        },
        removeGroupByColumn(index) {
            var vm = this;
            if (index >= 0 && index < vm.selectedGroupByColumns.length) {
                vm.selectedGroupByColumns.splice(index, 1);
            }
        },
        showFormatColumn(item) {
            var vm = this;
            return (vm.selectedTableHeader === 'tblDetailInvoiceFormat' ||
                   vm.selectedTableHeader === 'tblDetailedInvoiceFormat') &&
                   (item.dataType === 'Date Time' || item.dataType === 'Time');
        },
        isDateType(dataType) {
            return dataType === 'Date Time' || dataType === 'Time';
        },
        save() {
            var vm = this;
            // Validation
            var msg = '';
            vm.selectedColumns.forEach(item => {
                if (!item.displayName || item.displayName.trim() === '') {
                    msg += 'no';
                }
                if (vm.showFormatColumn(item) && (!item.formatType || item.formatType.trim() === '')) {
                    msg += 'no';
                }
            });
            // if (vm.selectedTableHeader === 'tblInvoiceSummary') {
            //     if (vm.selectedGroupByColumns.length === 0) {
            //         msg += 'no';
            //     }
            // }
            
            if (msg.length > 0) {
                // Show alert: Correct highlighted errors
                return;
            }
            
            if (vm.selectedColumns.length === 0) {
                // Show alert: Please select any column
                return;
            }
            
            // Prepare data
            var fieldData = vm.selectedColumns.map(item => ({
                templateId: item.templateId,
                columnName: item.columnName,
                displayName: item.displayName,
                dataType: item.dataType,
                tableName: item.tableName,
                fieldFrom: item.fieldFrom,
                formatType: item.formatType || ''
            }));

            if (vm.selectedTableHeader === 'tblInvoiceSummary') {
                var groupByData = vm.selectedGroupByColumns.map(item => ({
                    templateId: item.templateId,
                    columnName: item.columnName,
                    displayName: item.displayName,
                    dataType: item.dataType,
                    tableName: item.tableName,
                    fieldFrom: item.fieldFrom,
                    formatType: item.formatType || ''
                }));
                
                vm.$emit('save', {
                    summary: fieldData,
                    groupBy: groupByData
                });
            } else {
                vm.$emit('save', fieldData);
            }
        },
        close() {
            this.$emit('close');
        }
    }
}
</script>

<style scoped>
.mover-panel-box {
    flex: 1;
    min-width: 300px;
    margin: 10px;
}
.mover-controls {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px 10px;
}
.mover-controls button {
    background: none;
    border: 1px solid #ddd;
    padding: 10px;
    margin: 5px;
    cursor: pointer;
    border-radius: 4px;
}
.mover-controls button:hover {
    background: #f0f0f0;
}
.mandatory {
    color: red;
}
.legend-yellow {
    background-color: #fff3cd;
}
</style>

