<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="container-fluid padding-t_10">
            <div class="right-content right-content-expand">
                <div class="col-md-12 p-0">
                    <div class="tab-content" id="myTabContent">
                        <section class="page-action action-margin">
                            <div class="container-fluid p-0">
                                <div class="col-md-12 p-0">
                                    <div class="theme-primary partition p-actions-expand">
                                        <span class="p-name text-white">{{ $t('ManageTemplate') }}</span>
                                        <span class="p-actions float-right">
                                            <a @click="backUrl" class="p-action-btn text-white">
                                                <i class="fa fa-angle-double-left"></i><br />{{ $t('BacktoList') }}
                                            </a>
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </section>
                        
                        <div class="tab-pane call-detail fade border p-0 active show pb-3" id="template" role="tabpanel">
                            <div class="container-fluid">
                                <div id="divShowlistingData">
                                    <div class="col-md-12 p-0 pt-2">
                                        <div class="row">
                                            <div class="col-lg-4">
                                                <div class="form-group">
                                                    <input type="hidden" v-model="templateData.invoiceTemplateId" />
                                                    <label>{{ $t('InvoiceTemplateName') }}:<span class="mandatory">*</span></label>
                                                    <input
                                                        type="text"
                                                        v-model="templateData.invoiceTemplateName"
                                                        :class="['form-control', { 'is-invalid': isTemplateNameInvalid }]"
                                                        maxlength="200"
                                                        @keydown="preventEnter"
                                                    />
                                                    <span v-if="isTemplateNameInvalid" class="text-danger">{{ $t('Required').replace('{0}', $t('InvoiceTemplateName')) }}</span>
                                                </div>
                                            </div>
                                            <div class="col-lg-4" v-if="isShowWorkdayInvoiceType">
                                                <div class="form-group">
                                                    <label>{{ $t('CustomName') }}:</label>
                                                    <input
                                                        type="text"
                                                        v-model="templateData.workdayTemplateName"
                                                        class="form-control"
                                                        maxlength="200"
                                                        @keydown="preventEnter"
                                                    />
                                                </div>
                                            </div>
                                            <div class="col-lg-4">
                                                <div class="form-group">
                                                    <label>{{ $t('SelectSetup') }}:<span class="mandatory">*</span></label>
                                                    <select
                                                        v-model="SetupId"
                                                        :class="['form-control', { 'is-invalid': isSetupInvalid }]"
                                                        @change="onSetupChange"
                                                    >
                                                        <option value="">{{ $t('SelectSetup') }}</option>
                                                        <option
                                                            v-for="setup in setupList"
                                                            :key="setup.value"
                                                            :value="setup.value"
                                                        >
                                                            {{ setup.name }}
                                                        </option>
                                                    </select>
                                                    <span v-if="isSetupInvalid" class="text-danger">{{ $t('Required').replace('{0}', $t('Setup')) }}</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    
                                    <!-- Talygen backup Format (Detail Invoice Format) -->
                                    <div class="clearfix"></div>
                                    <div class="heading-border mt-3">
                                        <h5 class="h5">
                                            <span>{{ $t('DetailsInvoiceFormat') }}</span>
                                            <span class="text-danger">*</span>
                                            <small class="float-right">
                                                <a 
                                                    href="javascript:;" 
                                                    data-table="tblDetailInvoiceFormat" 
                                                    class="addtemplatemapping" 
                                                    :title="$t('Add') + ' ' + $t('DetailsInvoiceFormat') + ' ' + $t('Template')"
                                                    @click="openFieldMapping('tblDetailInvoiceFormat')"
                                                >
                                                    <i class="fa fa-plus pr-2" aria-hidden="true"></i>{{ $t('Add') }}
                                                </a>
                                            </small>
                                        </h5>
                                    </div>
                                    <span id="spnDetailInvoiceFormat" class="text-danger"></span>
                                    <div class="listing mt-3 mb-3">
                                        <div class="table-responsive">
                                            <table id="tblDetailInvoiceFormat" class="table table-bordered dt-responsive nowrap mt-0" style="width:100%;">
                                                <thead class="thead-bg">
                                                    <tr id="trDetailInvoiceFormat">
                                                        <th
                                                            v-for="(item, index) in templateData.detailInvoiceFormat"
                                                            :key="index"
                                                            :data-val="item.templateId"
                                                            :data-columnname="item.columnName"
                                                            :data-datatype="item.dataType"
                                                            :data-tablename="item.tableName"
                                                            :data-displayname="item.displayName"
                                                            :data-from="item.fieldFrom"
                                                            :data-formattype="item.formatType"
                                                        >
                                                            <span>{{ item.displayName }}</span>
                                                        </th>
                                                    </tr>
                                                </thead>
                                            </table>
                                        </div>
                                    </div>
                                    
                                    <!-- Invoice backup Format (Detailed Invoice Format) -->
                                    <div class="clearfix"></div>
                                    <div class="heading-border mt-3">
                                        <h5 class="h5">
                                            <span>{{ $t('DetailedInvoiceFormat') }}</span>
                                            <span class="text-danger">*</span>
                                            <small class="float-right">
                                                <a 
                                                    href="javascript:;" 
                                                    data-table="tblDetailedInvoiceFormat" 
                                                    class="addtemplatemapping" 
                                                    :title="$t('Add') + ' ' + $t('DetailedInvoiceFormat') + ' ' + $t('Template')"
                                                    @click="openFieldMapping('tblDetailedInvoiceFormat')"
                                                >
                                                    <i class="fa fa-plus pr-2" aria-hidden="true"></i>{{ $t('Add') }}
                                                </a>
                                            </small>
                                        </h5>
                                    </div>
                                    <span id="spnDetailedInvoiceFormat" class="text-danger"></span>
                                    <div class="listing mt-3 mb-3">
                                        <div class="table-responsive">
                                            <table id="tblDetailedInvoiceFormat" class="table table-bordered dt-responsive nowrap mt-0" style="width:100%;">
                                                <thead class="thead-bg">
                                                    <tr id="trDetailedInvoiceFormat">
                                                        <th
                                                            v-for="(item, index) in templateData.detailedInvoiceFormat"
                                                            :key="index"
                                                            :data-val="item.templateId"
                                                            :data-columnname="item.columnName"
                                                            :data-datatype="item.dataType"
                                                            :data-tablename="item.tableName"
                                                            :data-displayname="item.displayName"
                                                            :data-from="item.fieldFrom"
                                                            :data-formattype="item.formatType"
                                                        >
                                                            <span>{{ item.displayName }}</span>
                                                        </th>
                                                    </tr>
                                                </thead>
                                            </table>
                                        </div>
                                    </div>
                                    
                                    <!-- Summary Format -->
                                    <div class="clearfix"></div>
                                    <div class="heading-border mt-3">
                                        <h5 class="h5">
                                            <span>{{ $t('Summary') }}</span>
                                            <span class="text-danger">*</span>
                                            <small class="float-right">
                                                <a 
                                                    href="javascript:;" 
                                                    data-table="tblInvoiceSummary" 
                                                    class="addtemplatemapping" 
                                                    :title="$t('Add') + ' ' + $t('Summary') + ' ' + $t('Template')"
                                                    @click="openFieldMapping('tblInvoiceSummary')"
                                                >
                                                    <i class="fa fa-plus pr-2" aria-hidden="true"></i>{{ $t('Add') }}
                                                </a>
                                            </small>
                                        </h5>
                                    </div>
                                    <span id="spnSummaryInvoice" class="text-danger"></span>
                                    <span id="spnSummaryInvoiceGroupBy" class="text-danger"></span>
                                    <div class="listing mt-3 mb-3">
                                        <div class="table-responsive">
                                            <table id="tblInvoiceSummary" class="table table-bordered dt-responsive nowrap mt-0" style="width:100%;">
                                                <thead class="thead-bg">
                                                    <tr id="trInvoiceSummary">
                                                        <th
                                                            v-for="(item, index) in templateData.invoiceSummaryData"
                                                            :key="index"
                                                            :data-val="item.templateId"
                                                            :data-columnname="item.columnName"
                                                            :data-datatype="item.dataType"
                                                            :data-tablename="item.tableName"
                                                            :data-displayname="item.displayName"
                                                            :data-from="item.fieldFrom"
                                                            :data-formattype="item.formatType"
                                                        >
                                                            {{ item.displayName }}
                                                        </th>
                                                    </tr>
                                                </thead>
                                                <tfoot>
                                                    <tr>
                                                        <td class="notinclude">{{ $t('GroupBy') }}</td>
                                                        <td
                                                            v-for="(item, index) in templateData.invoiceSummaryGroupBy"
                                                            :key="index"
                                                            :data-val="item.templateId"
                                                            :data-columnname="item.columnName"
                                                            :data-datatype="item.dataType"
                                                            :data-tablename="item.tableName"
                                                            :data-displayname="item.displayName"
                                                            :data-from="item.fieldFrom"
                                                            :data-formattype="item.formatType"
                                                        >
                                                            {{ item.displayName }}
                                                        </td>
                                                    </tr>
                                                </tfoot>
                                            </table>
                                        </div>
                                    </div>
                                    
                                    <!-- Invoice Edit View -->
                                    <div class="clearfix"></div>
                                    <div class="heading-border mt-3">
                                        <h5 class="h5">
                                            <span>{{ $t('InvoiceEditMode') }}</span>
                                            <span class="text-danger">*</span>
                                            <small class="float-right">
                                                <a 
                                                    href="javascript:;" 
                                                    data-table="tblInvoiceEditMode" 
                                                    class="addtemplatemapping" 
                                                    :title="$t('Add') + ' ' + $t('InvoiceEditMode') + ' ' + $t('Template')"
                                                    @click="openFieldMapping('tblInvoiceEditMode')"
                                                >
                                                    <i class="fa fa-plus pr-2" aria-hidden="true"></i>{{ $t('Add') }}
                                                </a>
                                            </small>
                                        </h5>
                                    </div>
                                    <span id="spnInvoiceEditMode" class="text-danger"></span>
                                    <div class="listing mt-3 mb-3">
                                        <div class="table-responsive">
                                            <table id="tblInvoiceEditMode" class="table table-bordered dt-responsive nowrap mt-0" style="width:100%;">
                                                <thead class="thead-bg">
                                                    <tr id="trInvoiceEditMode">
                                                        <th
                                                            v-for="(item, index) in templateData.invoiceEditMode"
                                                            :key="index"
                                                            :data-val="item.templateId"
                                                            :data-columnname="item.columnName"
                                                            :data-datatype="item.dataType"
                                                            :data-tablename="item.tableName"
                                                            :data-displayname="item.displayName"
                                                            :data-from="item.fieldFrom"
                                                            :data-formattype="item.formatType"
                                                        >
                                                            <span>{{ item.displayName }}</span>
                                                        </th>
                                                    </tr>
                                                </thead>
                                            </table>
                                        </div>
                                    </div>
                                    
                                    <div class="clearfix"></div>
                                    <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right mt-2">
                                            <a href="javascript:;" id="btnSaveTemplate" @click="saveTemplate" :title="$t('Save')" class="btn btn-success mr-2">
                                                <i class="fa fa-save pr-2"></i>{{ $t('Save') }}
                                            </a>
                                            <a @click="backUrl" :title="$t('Cancel')" class="btn btn-danger">
                                                <i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}
                                            </a>
                                        </div>
                                        <div class="col-lg-6 mt-3">
                                            <span class="mandatory">{{ $t('MandatoryString') }}</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
        <!-- Template Field Mapping Modal -->
        <TemplateFieldMapping 
            v-if="showFieldMapping"
            :setupId="SetupId"
            :selectedTableHeader="selectedTableHeader"
            :existingFields="getExistingFieldsForTable(selectedTableHeader)"
            @close="closeFieldMapping"
            @save="onFieldMappingSave"
        />
    </section>
</template>

<script>
import DataService from '../../../services/DataService'
import  { locateBillingService } from '../../../services/locateBillingService'
import TemplateFieldMapping from './TemplateFieldMapping.vue';

export default {
    name: "AddEditTemplate",
    components: {
        TemplateFieldMapping
    },
    data() {
        return {
            isLoading: false,
            templateData: {
                invoiceTemplateId: 0,
                invoiceTemplateName: '',
                workdayTemplateName: '',
                setupId: null,
                detailInvoiceFormat: [],
                detailedInvoiceFormat: [],
                invoiceSummaryData: [],
                invoiceSummaryGroupBy: [],
                invoiceEditMode: []
            },
            setupList: [],
            isShowWorkdayInvoiceType: false,
            showFieldMapping: false,
            selectedTableHeader: '',
            templateId: null,
            isTemplateNameInvalid: false,
            isSetupInvalid: false
        }
    },
    created: function () {
        var vm = this;
        vm.templateId = vm.$route.query.templateId || null;
        vm.SetupId = vm.$route.query.setupId || null;
        vm.loadSetupList();
        vm.CheckFeaturesForCompany ();
    },
    methods: {
        CheckFeaturesForCompany: async function (type) {
            var vm = this;
            var appendingUrl = `featureCode=EnableWorkdayInvoiceType`;
            await DataService.CheckExtraFeatures(appendingUrl).then(function (response) {
                    if (response.data == true) {
                        vm.isShowWorkdayInvoiceType = false;
                    } else {
                        vm.isShowWorkdayInvoiceType = true;
                    }
            });
        },
         backUrl() {
            var vm = this;
            this.$router.push({ name: 'TemplateListing', params: { setupId: vm.SetupId } });
        },
        preventEnter(event) {
            if (event.keyCode === 13) {
                event.preventDefault();
                return false;
            }
        },
        loadSetupList() {
            var vm = this;
            vm.isLoading = true;
            // TODO: Replace with actual API call
            var params = `setupId=${vm.SetupId}&templateId=${vm.templateId}`;
            locateBillingService.GetSetupList(params).then((response) => {
                vm.isLoading = false;
                if (response.data && response.data.result.setUpList.length>0) {
                    vm.setupList = response.data.result.setUpList;
                    vm.templateData = response.data.result;
                    // Ensure arrays are initialized
                    vm.templateData.detailInvoiceFormat = vm.templateData.detailInvoiceFormat || [];
                    vm.templateData.detailedInvoiceFormat = vm.templateData.detailedInvoiceFormat || [];
                    vm.templateData.invoiceSummaryData = vm.templateData.invoiceSummaryData || [];
                    vm.templateData.invoiceSummaryGroupBy = vm.templateData.invoiceSummaryGroupBy || [];
                    vm.templateData.invoiceEditMode = vm.templateData.invoiceEditMode || [];
                }
            }).catch((error) => {
                vm.isLoading = false;
                console.error('Error loading setup list:', error);
            });
            
            // Placeholder
            setTimeout(() => {
                vm.isLoading = false;
                vm.setupList = [];
            }, 100);
        },
        onSetupChange() {
            // Setup changed, may need to reload available fields
        },
        openFieldMapping(tableName) {
            var vm = this;
            if (!vm.SetupId) {
                // Show alert: Please select setup first
                return;
            }
            vm.selectedTableHeader = tableName;
            vm.showFieldMapping = true;
        },
        closeFieldMapping() {
            this.showFieldMapping = false;
            this.selectedTableHeader = '';
        },
        getExistingFieldsForTable(tableName) {
            var vm = this;
            switch(tableName) {
                case 'tblDetailInvoiceFormat':
                    return vm.templateData.detailInvoiceFormat;
                case 'tblDetailedInvoiceFormat':
                    return vm.templateData.detailedInvoiceFormat;
                case 'tblInvoiceSummary':
                    return {
                        summary: vm.templateData.invoiceSummaryData,
                        groupBy: vm.templateData.invoiceSummaryGroupBy
                    };
                case 'tblInvoiceEditMode':
                    return vm.templateData.invoiceEditMode;
                default:
                    return [];
            }
        },
        onFieldMappingSave(fieldData) {
            var vm = this;
            switch(vm.selectedTableHeader) {
                case 'tblDetailInvoiceFormat':
                    vm.templateData.detailInvoiceFormat = fieldData;
                    break;
                case 'tblDetailedInvoiceFormat':
                    vm.templateData.detailedInvoiceFormat = fieldData;
                    break;
                case 'tblInvoiceSummary':
                    vm.templateData.invoiceSummaryData = fieldData.summary || [];
                    vm.templateData.invoiceSummaryGroupBy = fieldData.groupBy || [];
                    break;
                case 'tblInvoiceEditMode':
                    vm.templateData.invoiceEditMode = fieldData;
                    break;
            }
            vm.closeFieldMapping();
        },
        saveTemplate() {
            var vm = this;
            debugger
            // Validation
            var msg = '';
            vm.isTemplateNameInvalid = false;
            vm.isSetupInvalid = false;
            if (!vm.templateData.invoiceTemplateName || vm.templateData.invoiceTemplateName.trim() === '') {
                msg += 'no';
                vm.isTemplateNameInvalid = true;
            }
            if (!vm.SetupId) {
                msg += 'no';
                vm.isSetupInvalid = true;
            }
            if (vm.templateData.detailInvoiceFormat.length === 0) {
                msg += 'no';
                document.getElementById('spnDetailInvoiceFormat').textContent = vm.$t('Required').replace('{0}', vm.$t('DetailsInvoiceFormat'));
            } else {
                document.getElementById('spnDetailInvoiceFormat').textContent = '';
            }
            if (vm.templateData.detailedInvoiceFormat.length === 0) {
                msg += 'no';
                document.getElementById('spnDetailedInvoiceFormat').textContent = vm.$t('Required').replace('{0}', vm.$t('DetailedInvoiceFormat'));
            } else {
                document.getElementById('spnDetailedInvoiceFormat').textContent = '';
            }
            if (vm.templateData.invoiceSummaryData.length === 0) {
                msg += 'no';
                document.getElementById('spnSummaryInvoice').textContent = vm.$t('Required').replace('{0}', vm.$t('Summary'));
            } else {
                document.getElementById('spnSummaryInvoice').textContent = '';
            }
            if (vm.templateData.invoiceSummaryGroupBy.length === 0) {
                msg += 'no';
                document.getElementById('spnSummaryInvoiceGroupBy').textContent = vm.$t('Required').replace('{0}', vm.$t('GroupBy'));
            } else {
                document.getElementById('spnSummaryInvoiceGroupBy').textContent = '';
            }
            if (vm.templateData.invoiceEditMode.length === 0) {
                msg += 'no';
                document.getElementById('spnInvoiceEditMode').textContent = vm.$t('Required').replace('{0}', vm.$t('InvoiceEditMode'));
            } else {
                document.getElementById('spnInvoiceEditMode').textContent = '';
            }
            
            if (msg.length > 0) {
                vm.ShowAlert(vm.$t('CorrectHighlightedErrors'), 'warning', true, vm.$t('Alert'));
                return;
            }
            
            // Prepare data
            var saveData = {
                invoiceTemplateId: vm.templateData.invoiceTemplateId,
                invoiceTemplateName: vm.templateData.invoiceTemplateName,
                workdayTemplateName: vm.templateData.workdayTemplateName,
                setupId: vm.SetupId,
                detailInvoiceFormat: vm.templateData.detailInvoiceFormat.map(item => ({
                    ...item,
                    invoiceType: "DETAILINVOICE"
                })),
                detailedInvoiceFormat: vm.templateData.detailedInvoiceFormat.map(item => ({
                    ...item,
                    invoiceType: "CUSTOM"
                })),
                invoiceSummaryData: vm.templateData.invoiceSummaryData.map(item => ({
                    ...item,
                    invoiceType: "SUMMARYINVOICE"
                })),
                invoiceSummaryGroupBy: vm.templateData.invoiceSummaryGroupBy.map(item => ({
                    ...item,
                    invoiceType: "GROUPBY"
                })),
                invoiceEditMode: vm.templateData.invoiceEditMode.map(item => ({
                    ...item,
                    invoiceType: "INVOICEEDITMODE"
                }))
            };
            
            vm.isLoading = true;
            locateBillingService.SaveTemplate(saveData).then((response) => {
                vm.isLoading = false;
                if (response.data && response.data.status === 0) {
                    vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Template')]), "Success", true, vm.$t('Alert'));
                    vm.$router.push({ name: 'TemplateListing', params: { setupId: vm.SetupId } });
                } else if (response.data && response.data.status === 11) {
                  vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Template')]), "failure", true, vm.$t('Alert'));
                } else {
                      vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Service")]), "danger", true, vm.$t("Alert"));
                    vm.isLoading = false;
                }
            }).catch((error) => {
                vm.isLoading = false;
                console.error('Error saving template:', error);
            });
            
            // Placeholder
            setTimeout(() => {
                vm.isLoading = false;
            }, 100);
        }
    }
}
</script>

<style scoped>
.mandatory {
    color: red;
}
</style>

